import server from "@/utils/server";
import Vue from "vue";

const app = {
  namespaced: true,
  state: {
    contractsData: []
  },
  mutations: {
    setContractsData(state, payload) {
      state.contractsData = payload;
    }
  },
  actions: {
    // 获取contracts
    async getContractsData(context) {
      try {
        const msg = await server.get(`v1/contracts`);
        if (msg.data.code === 0) {
          // 给msg.data按照日期排序
          context.commit({
            type: "setContractsData",
            data: msg.data.data
          });
        }
        return msg.data;
      } catch (e) {
        Vue.prototype.$message.info("Network Error");
      }
    },
    // 获取指定期的数据
    async getTicker() {
      try {
        const res = await server.get(`v1/pub/ticker`);
        return res.data;
      } catch (e) {
        Vue.prototype.$message.info("Network Error");
      }
    }
  }
};

export default app;
